import React, { useState, useEffect } from 'react'
import {Row, Col, Container,Table} from "reactstrap";
import base_url from './api/bootapi';
import axios from "axios";
import { toast } from 'react-toastify';
import ReactPaginate from 'react-paginate';
import "./AllCustomers.css"


const Balance = () => {

  useEffect(() => {
    document.title = "Cash Book";

  }, []);

  const [customers, setCustomers] = useState([]);
  const [balance, setBalance] = useState([]);
  

 const Balance = () => {
    axios.get(`${base_url}/customer/balance`).then(
      (response) => {
        console.log(response.data);
        setCustomers(response.data);
      },

      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    );
  }

  const EndingBalance = () => {
    axios.get(`${base_url}/customer/endingBalance`).then(
      (response) => {
        console.log(response.data);
        setBalance(response.data);
      },

      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    )
  }
// pagination
const [pagenumber, setPageNumber] = useState(0)
   
const usersPage = 9
const pageVisited = pagenumber * usersPage
const pageCount = Math.ceil(customers.length/usersPage)
const changePage=({selected})=>{
    setPageNumber(selected)
}
 

  useEffect(() => {
    Balance();
    EndingBalance();
  }, []);

  const customerlist = customers.filter((val)=>{
    if (val.creditAmountReceived !== 0) {
    return val
  }
  }).map(customer => {
      return <tr key={customer.id}>
      <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.creditAmountReceived}</td>
    </tr>
  });

  const customerdebitlist = customers.filter((val) => {
    if (val.debitAmountReceived !== 0) {
      return val
    }
  }).map(customer => {
     return <tr key={customer.id}>
      <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.debitAmountReceived}</td>
    </tr>
  });


  return (
    <div>

      <br></br>
      <div>
        <form class="form-inline">
          <label for="endingBalance">Ending Balance:</label>
          <input type="text" id="endingBalance" readOnly="true" defaultValue={balance.endingBalance} readOnly="true" style={{ marginLeft: 5 }} />
          <label for="todayDate" style={{ marginLeft: 35 }}>Date:</label>
          <input type="date" id="todayDate" defaultValue={balance.todayDate} style={{ marginLeft: 5 }} readOnly="true" />
        </form>
      </div>
      <br></br>
      <Row>
        <Col md={6}>
          <Table className="mt-4">
            <thead>
              <tr>
                <th width="15%">Name</th>
                <th width="20%">Credit</th>
                <th width="15%">Date</th>
              </tr>
            </thead>
            <tbody>
              {customerlist}
            </tbody>
          </Table>
        </Col>
        <Col md={6} >
          <Table className="mt-4 offset-2">
            <thead>
              <tr>
                <th width="15%">Name</th>
                <th width="20%">Debit</th>
                <th width="15%">Date</th>
              </tr>
            </thead>
            <tbody>
              {customerdebitlist}
            </tbody>
          </Table>
        </Col>
      </Row>
       
       
    </div>
  )
}
export default Balance;